/* eslint-disable react/no-array-index-key */
import React from 'react';
import PropTypes from 'prop-types';

export default class ImageRing extends React.PureComponent {
  static propTypes = {
    images: PropTypes.array.isRequired,
    imageSize: PropTypes.number.isRequired,
  };

  getImages = () => {
    const { images, imageSize } = this.props;
    const radius = imageSize * 0.8;
    const degreesPerImage = 360 / images.length;
    return images.map((image, i) => {
      const rotation = (degreesPerImage * i + 90);
      const style = {
        width: `${100/2.5}%`,
        paddingTop: `${100/2.5}%`,
        // height: imageSize,
        // margin: -imageSize / 2,
        transform: `rotate(${rotation}deg) translate(${100/2.5}%) rotate(-${rotation}deg)`,
        zIndex: (i % 2) + 1,
        backgroundImage: `url(${image})`,
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover',
      };
      return (
        <span className="banner-image" style={style} key={`ring-image-${i}`} />
      );
    });
  };

  render() {
    const { imageSize } = this.props;
    return (
      <div className="banner-image-wheel" style={{ height: imageSize * 2.6 }}>
        { this.getImages() }
      </div>
    );
  }
}
